import PropTypes from 'prop-types';
import React, { useState } from 'react';
import styled from 'styled-components';
import { InputNumber, Popover, Statistic, Switch } from 'antd';
import { round } from 'lodash';

import { CardGrid } from '../Layouts/CardLayout';
import { getAspdFrequency } from '../../utils/aspd';

const MarginDiv = styled.div`
  margin-bottom: 5px;
`;

function CastTime({ castTime }) {
  const [skillCastTime, setSkillCastTime] = useState(0);
  const [equipCastTime, setEquipCastTime] = useState(0);
  const [isSource, setIsSource] = useState(true);

  return (
    <div>
      <p></p>
      <MarginDiv>
        {' '}
        <Switch
          size="small"
          checked={isSource}
          onChange={checked => setIsSource(checked)}
        />
      </MarginDiv>
      <MarginDiv>
        <span> : </span>
        <InputNumber
          size="small"
          min={0}
          max={30}
          value={skillCastTime}
          onChange={setSkillCastTime}
        />
      </MarginDiv>
      <MarginDiv>
        <span> : </span>
        <InputNumber
          size="small"
          min={0}
          max={100}
          value={equipCastTime}
          onChange={setEquipCastTime}
        />
        %
      </MarginDiv>
      <p>
         :{' '}
        {round(
          skillCastTime *
            (1 - equipCastTime / 100) *
            (isSource ? 0.6 : 1) *
            castTime,
          2
        )}
      </p>
    </div>
  );
}

CastTime.propTypes = {
  castTime: PropTypes.number.isRequired,
};

const getPopoverProps = ({ label, aspd, castTime }) => {
  switch (label) {
    default:
    case 'ASPD':
      return {
        title: 'ASPD ()',
        content: `${getAspdFrequency(aspd)}`,
      };

    case 'MaxHP':
      return {
        title: 'Max HP ()',
        content: '',
      };

    case 'MaxSP':
      return {
        title: 'Max SP ()',
        content: '',
      };

    case 'ATK':
      return {
        title: 'ATK ()',
        content: '',
      };

    case 'MATK':
      return {
        title: 'MATK ()',
        content: '',
      };

    case 'DEF':
      return {
        title: 'DEF ()',
        content: (
          <>
            <p></p>
            <p> [(4000 + def) / (4000 + def * 10)]</p>
          </>
        ),
      };

    case 'MDEF':
      return {
        title: 'MDEF ()',
        content: (
          <>
            <p></p>
            <p> [(1000 + def) / (1000 + def * 10)]</p>
          </>
        ),
      };

    case 'CRI':
      return {
        title: 'CRI ()',
        content: (
          <>
            <p></p>
            <p>RO </p>
          </>
        ),
      };

    case 'HIT':
      return {
        title: 'HIT ()',
        content: '',
      };

    case 'FLEE':
      return {
        title: 'FLEE ()',
        content: (
          <>
            
            <br />
            
            <br />
            
          </>
        ),
      };

    case 'CastTime':
      return {
        title: 'Case Time ()',
        content: <CastTime castTime={castTime} />,
      };
  }
};

function AbilityTip({ label, children, suffix, ...props }) {
  return (
    <CardGrid>
      <Statistic
        title={
          <Popover {...getPopoverProps({ label, ...props })}>{label}</Popover>
        }
        value={children}
        suffix={suffix}
      />
    </CardGrid>
  );
}

AbilityTip.propTypes = {
  children: PropTypes.node.isRequired,
  label: PropTypes.string.isRequired,
  suffix: PropTypes.node,
};

AbilityTip.defaultProps = {
  suffix: null,
};

export { AbilityTip };
